.overlay{position:fixed;z-index:99;width:100vw;height:100vh;display:none;align-items:center;justify-content:center;background-color:rgba(7,27,44,.6784313725)}.overlay .card{height:100%;max-height:600px;min-width:360px;width:80%;max-width:1080px;background-color:#233748;border-radius:20px;overflow:auto;position:relative;display:flex;flex-wrap:wrap;animation:showCard .5s ease-in-out 1}@media(max-width: 820px){.overlay .card{height:max-content;max-height:800px}}@media(max-width: 420px){.overlay .card{height:max-content;max-height:650px}}.overlay .card .close_button{width:30px;height:30px;position:absolute;z-index:100;top:5%;left:3%;cursor:pointer}@media(max-width: 820px){.overlay .card .close_button{top:5%;right:7%;left:unset}}.overlay .card img:nth-child(2){width:360px;height:100%;object-fit:contain;background-image:url(../../../../public/img/assort/bck-product-block.webp);background-size:cover}@media(max-width: 820px){.overlay .card img:nth-child(2){width:100%;height:400px}}@media(max-width: 420px){.overlay .card img:nth-child(2){width:100%;height:240px}}.overlay .card .description_block{height:100%;flex:1;padding:20px}.overlay .card .description_block h3{margin:20px 0 30px;font-size:24px;text-transform:uppercase;font-weight:600}@media(max-width: 620px){.overlay .card .description_block h3{margin:0 0 30px}}.overlay .card .description_block .add_block{display:flex;width:100%;flex-wrap:wrap;gap:40px}@media(max-width: 620px){.overlay .card .description_block .add_block{gap:10px 40px;justify-content:center}}.overlay .card .description_block .add_block div{display:flex;flex-direction:column;width:max-content}.overlay .card .description_block .add_block div p{margin:0%;font-size:16px;color:#9cadbe}.overlay .card .description_block .add_block div span{width:max-content;font-size:30px;color:#e6007e;font-weight:600}.overlay .card .description_block .add_block .specials{display:flex;justify-content:center;flex-direction:row;flex-wrap:wrap;gap:5px 10px;width:max-content}.overlay .card .description_block .add_block .specials img[alt=Особенность]{width:25px;height:25px;background-image:none}.overlay .card .description_block .add_block .add{position:relative;width:max-content;height:40px;background-color:#e6007e;border-radius:20px;font-size:22px;color:rgba(0,0,0,0);border:none}.overlay .card .description_block .add_block .add::before{z-index:0;content:"";width:100%;height:100%;background-color:#233748;position:absolute;top:0;left:0;border-radius:20px;transform:scale(0%);transition:transform .2s ease-in-out}.overlay .card .description_block .add_block .add::after{content:"ДОБАВИТЬ В КОРЗИНУ";font-size:20px;line-height:20px;position:absolute;top:10px;left:17px;color:#fff}.overlay .card .description_block .add_block .add:active{transform:scale(95%)}.overlay .card .description_block .add_block .add:hover::before{box-shadow:0px 0px 30px #e6007e;transform:scale(98%, 90%)}.overlay .card .description_block .description{width:100%}.overlay .card .description_block .description:last-of-type{margin-bottom:0}.overlay .card .description_block .info_button{color:#071b2c;background-color:#fff;font-size:20px;font-weight:600;margin-right:20px;border-radius:10px;border:none;padding:5px 10px}.show{display:flex}@keyframes showCard{0%{transform:translateY(100%)}100%{transform:translateY(0%)}}