.card{height:330px;min-width:210px;width:100%;background-color:#233748;border-radius:20px;overflow:hidden;position:relative;display:grid;grid-template-rows:200px 60px 70px;grid-template-columns:1fr}.card img{width:100%;height:100%;object-fit:contain;background-image:url(../../../../public/img/assort/bck-product-block.webp);cursor:pointer}.card h3{margin:0%;padding:10px 15px;font-size:14px;text-transform:uppercase;font-weight:600}.card .short_description{display:flex;width:100%;justify-content:space-between;align-items:center;padding:0 15px;gap:8px}.card .short_description div:first-child{display:flex;flex-direction:column}.card .short_description div:first-child p{margin:0%;font-size:12px;color:#9cadbe}.card .short_description div:first-child span{width:max-content;font-size:22px;color:#e6007e;font-weight:600}.card .short_description div:nth-child(2){display:flex;justify-content:center;flex-wrap:wrap;width:50%;gap:5px 10px}.card .short_description div:nth-child(2) img[alt=Особенность]{width:25px;height:25px;background-image:none}.card .short_description .add{position:relative;min-width:40px;height:40px;background-color:#e6007e;border-radius:50%;border:none;font-size:20px}.card .short_description .add::before{z-index:0;content:"";width:40px;height:40px;background-color:#233748;position:absolute;top:0;left:0;border-radius:50%;transform:scale(0%);transition:transform .2s ease-in-out}.card .short_description .add::after{content:"+";font-size:35px;line-height:20px;position:absolute;top:11px;left:9.5px}.card .short_description .add:active{transform:scale(90%)}.card .short_description .add:hover::before{box-shadow:0px 0px 30px #e6007e;transform:scale(90%)}