.logo_container{cursor:pointer}.logo_container a{display:flex;justify-content:center;align-items:center;gap:10px}.logo_container a img[alt=Логотип]{height:55px;width:55px}.logo_container a .company_name{margin:0%;font-size:24px;font-weight:600;text-shadow:rgba(0,0,0,.5) 0px 0px 26px}.logo_container a .company_name span{position:relative;font-size:22px}.logo_container a .company_name span::after{content:".";position:absolute;top:-2.3px;right:-0.5px;color:#e6007e;font-size:30px;line-height:0%}